<template>
  <div class="left-layout-container" v-if="gitHubUser">
  <img @click="home" class="avatar" src="/static/img/avatar.png">
    <div class="login-name">Justice_Eternal</div>
   <!--  <div class="bio">{{gitHubUser.bio}}</div> -->
    <ul class="other-site">
      <li v-for="site in thirdPartySite" :key="site.url" @click="openThirdPartySite(site.url)">
        <img :src="site.img">
      </li>
    </ul>
    <ul class="left-menu">
      <router-link :class="isBlog ? 'selected-menu':''" tag="li" to="/Blog">谱曲资源站</router-link>
      <router-link :class="isInstruction ? 'selected-menu':''" tag="li" to="/instruction">使用说明</router-link>
      <router-link :class="isAboutMe ? 'selected-menu':''" tag="li" to="/AboutMe">关于JE</router-link>
      <el-row class="tac">
        <el-col :span="8">
          <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
            <el-submenu index="1">
              <template slot="title">视频欣赏</template>
                <el-menu-item index="1-1"><router-link  to="/Bilibili">南里沙</router-link></el-menu-item>
                <el-menu-item index="1-2"><router-link  to="/Bilibili1">千本樱</router-link></el-menu-item>
                <el-menu-item index="1-3"><router-link  to="/Bilibili2">Flower Dance</router-link></el-menu-item>
                <el-menu-item index="1-4"><router-link  to="/Bilibili3">未闻花名</router-link></el-menu-item>
                <el-menu-item index="1-5"><router-link  to="/Bilibili4">Fate</router-link></el-menu-item>
            </el-submenu>
          </el-menu>
        </el-col>
      </el-row>

    </ul>
    <div v-if="showQQGroup" class="qq-group">
      <span>JE乐谱制作</span>
      <span> QQ群</span>
      <img src="/static/img/qq-group.png">
    </div>
  </div>
</template>
<style lang="scss" scoped>
  $indicator-color: #d6504f;

  .left-layout-container {
    position: fixed;
    left: 0px;
    top: 0px;
    bottom: 0px;
    width: 199px;
    background-color: #ffffff;
    border-right: 1px solid #eeeeee;
  }

  .avatar {
    width: 70px;
    height: 70px;
    border-radius: 10px;
    margin-top: 50px;
    margin-left: 65px;
    cursor: pointer;
  }

  .login-name {
    text-align: center;
    margin: 14px auto;
    margin-bottom: 12px;
    font-size: 14px;
    height: 16px;
    line-height: 16px;
    color: #4b595f;
  }

  .bio {
    text-align: center;
    margin: 0px auto;
    font-size: 13px;
    height: 18px;
    line-height: 18px;
    color: #849aa4;
  }

  .other-site {
    list-style: none;
    margin-top: 20px;
    margin-bottom: 67px;
    height: 20px;
    display: flex;
    justify-content: center;
    li {
      cursor: pointer;
      margin-right: 20px;
      img {
        width: 20px;
        height: 20px;
      }
    }
    li:last-child {
      margin-right: 0px;
    }
  }

  .left-menu {
    // 上面总的高度为 290px，下面 QQ 群的 margin + 高度为 240px
    height: calc(100% - 290px - 240px);
    min-height: 150px;
    list-style: none;
    color: #4b595f;
    li {
      width: 199px;
      height: 60px;
      line-height: 60px;
      cursor: pointer;
      font-size: 16px;
      &:before {
        width: 4px;
        height: 100%;
        content: '';
        margin-right: 46px;
        float: left;
        display: table;
      }
      &:hover {
        &:before {
          background-color: $indicator-color;
        }
      }
    }
    :first-child {
      margin-bottom: 0px;
    }
  }

  .selected-menu {
    background-color: #e5f1fc;
    color: $indicator-color;
    &:before {
      background-color: $indicator-color;
    }
  }

  .qq-group {
    margin-top: 50px;
    margin-left: 55px;
    margin-bottom: 50px;
    height: 140px;
    width: 90px;
    span {
      display: block;
      width: 100%;
      height: 15px;
      line-height: 15px;
      font-size: 12px;
      text-align: center;
      color: #849aa4;
      margin-bottom: 10px;
    }
    span:first-child {
      color: #4b595f;
    }
    img {
      width: 90px;
      height: 90px;
    }
  }
</style>
<script>
  import { mapGetters } from 'vuex'

  export default {
    computed: {
      ...mapGetters([
        'gitHubUser',
        'showQQGroup',
        'thirdPartySite'
      ]),
      isBlog () {
        return this.$route.name && this.$route.name.startsWith('Blog')
      },
      isAboutMe () {
        return this.$route.name === 'AboutMe'
      },
      isBilibili () {
        return this.$route.name === 'Bilibili'
      },
      isInstruction () {
        return this.$route.name === 'instruction'
      }
    },
    methods: {
      home () {
        this.$router.push('/')
      },
      openThirdPartySite (url) {
        window.open(url)
      },
      handleOpen (key, keyPath) {
        console.log(key, keyPath)
      },
      handleClose (key, keyPath) {
        console.log(key, keyPath)
      }
    }
  }
</script>



